                            status:"3"
                        },
                        success:(res)=>{
                            if(res.data.code===200){
                                this.tools.globalFn.publicToastFn(this,"");
                                //item,
                                item.status="3";
                            }
                        },
                        fail:(err)=>{
                            console.log(err);
                        }
                    })

                }).catch(() => {
                    // 
                });
