<template>
    <div class="publicAllContentBg" id="myRewardContent">
        <!---->
        <van-nav-bar title="" class="globalNavBarHeaderClass"
                     left-text=""
                     left-arrow
                     @click-left="backLastPage"
                     fixed
        >
        </van-nav-bar>
        <!---->
        <div class="contentColorBg">
            <p class="status" v-text="orderStatusText(orderContentData)"></p>
        </div>
        <van-loading type="spinner" color="black" v-if="orderLoadingDone===false"
                     class="globalLoadingAnmClass"></van-loading>
        <!---->
        <div class="orderListStyle" v-if="orderLoadingDone">
            <!---->
            <div class="header">
                <div class="left">
                    <span class="company" v-if="orderContentData.taskType===1"></span>
                    <span class="person" v-if="orderContentData.taskType===0"></span>
                </div>
                <!--<div class="right" v-text="orderStatusText(orderContentData)"></div>-->
            </div>
            <!---->
            <ul class="list">
                <li>
                    <div class="orderListGoodsStyle">
                        <!---->
                        <div class="img">
                            <img :src="orderContentData.img" alt="">
                        </div>
                        <!---->
                        <div class="info">
                            <div class="top">
                                <p class="title">{{orderContentData.title}}</p>
                                <!--<p class="realPrice">{{(orderContentData.price/100).toFixed(2)}}-->
                                <!--&lt;!&ndash;&ndash;&gt;-->
                                <!--&lt;!&ndash;<br v-if="orderContentData.zkInfo.zk===true"/><span v-if="orderContentData.zkInfo.zk===true" v-text="getOldPriceFn(orderContentData)"></span>&ndash;&gt;-->
                                <!--</p>-->
                            </div>
                            <div class="bottom">
                                <p class="gg">:{{(orderContentData.deposit/100).toFixed(2)}}</p>
                                <p class="num">{{(orderContentData.price/100).toFixed(2)}}</p>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <!---->
        <van-steps :active="taskStepData.nowStep" active-color="#30aaff" direction="vertical" v-if="orderLoadingDone" style="margin-bottom: 10px">
            <van-step v-for="item in taskStepData.list" :key="item.id">{{item.title}}</van-step>
        </van-steps>
        <!---->
        <van-cell-group v-if="orderLoadingDone">
            <!--<van-cell title="" :value="orderContentData.orderTakeMan" />-->
            <van-cell title="" :value="orderContentData.orderNum" />
            <van-cell title="" :value="orderContentData.cTime"/>
        </van-cell-group>
    </div>
</template>

<script>
    export default {
        name: 'myRewardContent',
        data() {
            return {
                //
                orderLoadingDone:false,
                //
                taskStepData: {
                    //
                    nowStep: 0,
                    //
                    list: [
                        {
                            id: 1,
                            title: ""
                        },
                        {
                            id: 2,
                            title: ""
                        },
                        {
                            id: 3,
                            title: ""
                        },
                        {
                            id: 4,
                            title: ""
                        }
                    ]
                },
                //
                firstLoadingStatus:false,
                //    
                orderContentData:{
                    // id: 4,
                    // //
                    // title: "",
                    // //
                    // price: 0,
                    // //
                    // deposit: 0,
                    // //()10
                    // taskType: 0,
                    // //
                    // // isOpen:true,
                    // //
                    // taskStatus: "1",
                    // //
                    // img: "",
                }
            }
        },
        methods: {
            //
            orderStatusText(orderContentData) {
                // if(orderContentData.isOpen===false){
                //     return "";
                // }
                switch (orderContentData.taskStatus) {
                    case "1":
                        return "";
                    case "2":
                        return "";
                    case "3":
                        return "";
                    case "4":
                        return "";
                    case "5":
                        return "";
                }
            },
            //
            backLastPage() {
                this.$router.go(-1);
            },
            //
            getFirstData(){
                this.tools.globalFn.publicAxiosFn(this,{
                    headerNeed:"noNeed",
                    url:"/rewardorder/myorderdetails",
                    baseUrl:this.tools.ajaxAddress.other,
                    data:{
                        token:this.$store.state===0?JSON.parse(localStorage.shopSaveObject).token:"MTU2ODk3MTk0NjR8MTIzNDU2Nzg5",
                        roid:this.$route.params.id
                    },
                    success:(res)=>{
                        console.log(res);
                        if(res.data.code===200){
                            this.orderContentData={
                                id: res.data.result.id,
                                //
                                title: res.data.result.title,
                                //
                                price: res.data.result.price,
                                //
                                deposit: res.data.result.earnest_money,
                                //()10
                                taskType: res.data.result.iscompany,
                                //
                                // isOpen:true,
                                //
                                taskStatus: res.data.result.status,

                                //
                                img:res.data.result.imgs.split("|")[0],
                                //
                                orderTakeMan:res.data.result.aname,
                                //
                                orderNum:res.data.result.ordernum,
                                //
                                cTime:res.data.result.ctime
                            };
                            //
                            this.taskStepData.nowStep=parseInt(res.data.result.status)-1;
                            //
                            this.orderLoadingDone=true;
                        }


                    },
                    fail:(err)=>{
                        console.log(err);
                    }
                });
                // setTimeout(()=>{
                //     this.orderContentData={
                //         id: 4,
                //         //
                //         title: "",
                //         //
                //         price: 203254,
                //         //
                //         deposit: 10000,
                //         //()10
                //         taskType: 0,
                //         //
                //         isOpen:true,
                //         //
                //         taskStatus: "published",
                //         //
                //         img: "//img-beta.tobebetterman.com/media/catalog/product/cache/bd935443df1c50537d4edaab4af5d446/296/0/k/jt/kjtc3knwprncdgb1538290486.jpg",
                //     };
                //     this.firstLoadingStatus=true;
                // },500);

            }
        },
        created() {
            this.getFirstData();
        },
        mounted() {

        },
        watch: {}
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /**/
    .orderListStyle {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        background-color: #fff;
        margin-bottom: 10px;
    }

    .orderListStyle > div.header {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 8px 10px;
    }

    .orderListStyle > div.header > div.left {
        float: left;
        color: #333;
        font-size: 14px;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    .orderListStyle > div.header > div.left .van-icon {
        font-size: 12px;
    }
    .orderListStyle > div.header > div.left span.company {
        font-size: 12px;
        color: #fafafa;
        padding: 4px 6px;
        background-color: #47b3ff;
        border-radius: 2px;
        display: inline-block;
    }
    .orderListStyle > div.header > div.left span.person {
        font-size: 12px;
        color: #fafafa;
        padding: 4px 6px;
        background-color: #f44;
        border-radius: 2px;
        display: inline-block;
    }
    .orderListStyle > div.header > div.right {
        float: right;
        color: #333;
        font-size: 14px;
    }

    .orderListStyle > ul.list {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    .orderListStyle > ul.list > li {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        border-top: 1px solid #f3f3f3;
    }

    .orderListStyle > ul.list > li:first-child {
        border-top: none;
    }

    /**/
    .orderListGoodsStyle {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        background-color: #fafafa;
        padding: 10px;
        margin-bottom: 10px;
    }

    .orderListGoodsStyle div.img {
        width: 20%;
        padding-top: 20%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
        float: left;
        background-color: #fafafa;
    }

    .orderListGoodsStyle div.img img {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
    }

    .orderListGoodsStyle div.info {
        width: 78%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
        float: right;
    }

    .orderListGoodsStyle div.info > div.top {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
    }

    .orderListGoodsStyle div.info > div.top > p.title {
        width: 100%;
        float: left;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
        text-overflow: ellipsis;
        -webkit-line-clamp: 2;
        white-space: normal;
        -webkit-box-orient: vertical;
        word-break: break-all;
        display: -webkit-box;
        font-size: 14px;
        color: #333;
    }

    .orderListGoodsStyle div.info > div.top > p.realPrice {
        width: 30%;
        color: #333;
        float: right;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
        font-size: 14px;
        text-align: right;
        font-weight: bold;
        white-space: nowrap;
        text-overflow: ellipsis;
    }

    .orderListGoodsStyle div.info > div.top > p.realPrice span {
        color: #999;
        text-decoration: line-through;
        font-size: 12px;
    }

    .orderListGoodsStyle div.info > div.bottom {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
        display: flex;
        align-orderContentDatas: center;
        justify-content: space-between;
    }

    .orderListGoodsStyle div.info > div.bottom > p.gg {
        /*float: left;*/
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        font-size: 12px;
        color: #999;
    }

    .orderListGoodsStyle div.info > div.bottom > p.num {
        /*float: right;*/
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        font-size: 14px;
        color: #333;
        font-weight: 400;
    }
    .contentBg{
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        background-color: #fff;
        padding: 10px;
    }
    .contentColorBg{
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        background: linear-gradient(to right,#30aaff,#2BD0FE);
        padding: 10px;
        margin-bottom: 10px;
    }
    .contentColorBg>p.status{
        color: #fafafa;
        font-size: 14px;
        padding-left: 10px;
    }
</style>



