        position: absolute;
        left: 0;
        bottom: 0;
    }

    .goodsListStyle > div.goodInfo .shopInfo {
        width: 80%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
