                                                </p>
                                            </div>
                                            <div class="bottom">
                                                <!--<p class="gg">{{item.ggName}}</p>-->
                                                <p class="num">x{{item.qty}}</p>
                                            </div>
                                        </div>
                                    </div>
                                </li>
                            </ul>
                            <!---->
                            <div class="orderTotalBox">
                                <div class="body">
