                                    <!--<span>:<b>{{(order.total/100).toFixed(2)}}</b></span>-->
                                    <!--<span>(<b>{{(order.yf/100).toFixed(2)}}</b>)</span>-->
                                    <span>:<b>{{order.grand_total}}</b></span>
                                    <span>(<b>{{order.shipping_total}}</b>)</span>
                                </div>
                            </div>
                            <!---->
                            <div class="orderButtonBox">
                                <!--<span class="del" v-if="order.order_status==='completed'"></span>-->
                                <span class="cancel" v-if="order.order_status==='payment_pending'" @click="cancelOrderFn(order)"></span>
                                <span class="pay" v-if="order.order_status==='payment_pending'" @click="goPayFn(order)"></span>
                                <span class="confirmGet" v-if="order.order_status==='dispatched'" @click="getConfirm(order)"></span>
                                <span class="checkShip" v-if="order.order_status==='dispatched'"></span>
                                <span class="evaluate" v-if="order.order_status==='review_pending'" @click="goToEva(order)"></span>
                                <span class="check" @click="goToOrderContentFn(order)"></span>
