                console.log(order);
                // order.increment_id
                //
                this.$bridge.callHandler("confirmOrderFn",order.pack_sn,(res)=>{
                    //type
                    // localStorage.orderListType="0";
                    if(res==="true"){
                        //
                        // alert("");
                        this.$router.replace({name:"orderList"});
                    }
                    else{
                        // alert("");
                        this.$router.replace({name:"orderList"});
                    }
                });
            },
            //
            cancelOrderFn(order){
                console.log(order);
                //
                this.$dialog.confirm({
                    title: '',
                    message: '?'
                }).then(() => {
                    //
                    console.log("");
                    this.tools.globalFn.publicAxiosFn(this,{
                        needLogin:true,
                        url:"/customer/order/cancel",
                        method:"POST",
                        data:{
                            order_sn:order.pack_sn,
                            module_type:1,
                            is_pack:1
                        },
                        success:(res)=>{
                            if(res.data.code===200){
                                this.tools.globalFn.publicToastFn(this,"");
                                this.firstDataStatusAnm=false;
                                //data
                                this.getFirstData();
                            }
                        }
                    })
                }).catch(() => {
                    // on cancel
                    console.log("");
                });
