        created() {
            //
            this.getFirstData();
        },
        mounted() {

        },
        watch: {},
        beforeRouteEnter(to,from,next){
            if(from.name==="userCenter"){
                //
                to.meta.useOldData=true;
            }
            else if(from.name==="orderListContent"){
                //
                to.meta.useOldData=false;
            }
            next();
        },
        activated(){
            if(this.$route.meta.useOldData===true){
                this.getFirstData();
            }
            this.$route.meta.useOldData=false;
        },
        deactivated(){

