        makeUploadImgFormDataFn(type,file){
            /*
            * type:
            * [
                 => 1,    // ,,
                 => 2,    // 
                 => 3,    // ,logo,
                 => 4,    // 
                 => 5,    // 
                   => 6,    // 
                 => 7,    // 
                ]
                file:,
            * */
            let formData=new FormData();
            //type
            formData.append('type',type);
            //
            formData.append('file',file);
            return formData;
