        //
        publicChangeLocalStorageFn(key,val,type){
            /*
            * key:key
            * val:val
            * type:edit:key;add:key;search:key
            * -1:;true:;false:
            * */
            // alert(key);
            // alert(JSON.parse(localStorage.shopSaveObject)[key]);
            // console.log("");
            //shopSaveObject
            if(localStorage.shopSaveObject===undefined){
                localStorage.shopSaveObject=JSON.stringify({});
            }
            if(JSON.parse(localStorage.shopSaveObject)[key]===undefined){
                // alert("key");
                // console.log("key");
                //-1
                if(type==="search"){
                    return -1;
                }
            }
            // console.log("");
            //
            let thisObj=JSON.parse(localStorage.shopSaveObject);
            // console.log("json");
            // console.log(thisObj);
            //,type
            if(type==="add"){
                // console.log("add");
                //
                thisObj[key]=val;
                //
                localStorage.shopSaveObject=JSON.stringify(thisObj);
                //
                thisObj=null;
                return true;
            }
            //
            if(type==="edit"){
                // console.log("");
                //
                thisObj[key]=val;
                //
                localStorage.shopSaveObject=JSON.stringify(thisObj);
                //
                thisObj=null;
                return true;
            }
            //false
            return false;
