<template>
    <div class="publicAllContentBg" id="searchPage">
        <!---->
        <van-nav-bar class="globalNavBarHeaderClass"
                     @click-left="backLastPage"
                     @click-right="searchConfirm"
                     fixed
        >
            <van-icon name="arrow-left" slot="left" class="globalNavBarIconClass"></van-icon>
            <van-field left-icon="search" placeholder="" slot="title" v-model="userSearchVal" clearable></van-field>
            <!--<van-icon name="search" slot="right" style="color: #fafafa" @click="searchConfirm"></van-icon>-->
            <span slot="right" class="globalNavBarIconClass text"></span>
        </van-nav-bar>
        <!---->
        <div class="hotSearchKeywordBox">
            <p class="header"></p>
            <ul class="list">
                <li v-for="(item,index) in hotSearchData" :key="index">
                    <p @click="publicSearchWordFn(item)">{{item}}</p>
                </li>
            </ul>
            <p class="noContent" v-if="hotSearchData.length===0"></p>
        </div>
        <!---->
        <div class="historySearchBox">
            <p class="header"></p>
            <!--<van-cell-group v-if="searchHistoryData.length>0">-->
            <!--<van-cell :value="item.name" v-for="item in searchHistoryData" is-link @click="wordSearchFn(item)"></van-cell>-->
            <!--</van-cell-group>-->
            <!--<van-cell-swipe  :right-width="65" v-for="item in searchHistoryData" :key="item.id">-->
                <!--&lt;!&ndash;<span slot="left"></span>&ndash;&gt;-->
                <!--<van-cell :title="item.name"></van-cell>-->
                <!--<span slot="right" class="delButton" @click="delThisHistory(item)"></span>-->
            <!--</van-cell-swipe>-->
            <van-swipe-cell :right-width="65" v-for="(item,index) in searchHistoryData" :key="index">
                    <van-cell :title="item.search_text" @click="publicSearchWordFn(item.search_text)"></van-cell>
                    <span slot="right" class="delButton" @click="delThisHistory(item)"></span>
            </van-swipe-cell>
            <p class="noContent" v-if="searchHistoryData.length===0"></p>
        </div>
        <!---->
        <div class="clearHistoryBox" v-if="searchHistoryData.length>0">
            <!--<div class="clearHistoryBox">-->
            <van-button size="large" @click="clearHistoryFn()">
                <van-icon name="delete" style="margin-right: 6px;vertical-align:sub;"></van-icon>
            </van-button>
        </div>
        <!---->
        <van-dialog v-model="confirmShowStatus" message="" show-cancel-button @confirm="clearHistoryConfirm" @cancel="clearHistoryCancel">
        </van-dialog>
    </div>
</template>

<script>
    export default {
        name: 'orderList',
        data() {
            return {
                //
                confirmShowStatus: false,
                //
                userSearchVal: "",
                //
                hotSearchData: [],
                //
                searchHistoryData: []
            }
        },
        methods: {
            //
            publicSearchWordFn(val,type){
                console.log(val);
                // console.log(type);
                //val:
                //type12
                this.tools.globalFn.publicChangeLocalStorageFn("globalSearchVal",JSON.stringify({
                    type:"keyword",
                    val:val
                }),"add");
                //
                this.$router.push({name:"searchResult"});
            },
            //
            historySearchFn(item){
                console.log(item);
            },
            //
            delThisHistory(item){
                console.log(item);
                //
                this.tools.globalFn.publicAxiosFn(this,{
                    url:"/catalogsearch/index/delsearch",
                    needLogin:true,
                    data:{
                        search_id:item.search_id
                    },
                    success:(res)=>{
                        if(res.data.code===200){
                            //
                            this.searchHistoryData.splice(this.searchHistoryData.indexOf(item),1);
                        }
                    }
                });
            },
            //
            // beforeCloseFn(act, done) {
            //     console.log(act);
            //     if(act === "cancel") {
            //         done();
            //     } else {
            //         done(false);
            //     }
            // },
            //
            clearHistoryConfirm() {
                console.log("");
                //
                this.tools.globalFn.publicAxiosFn(this,{
                    url:"/catalogsearch/index/delsearch",
                    needLogin:true,
                    success:(res)=>{
                        if(res.data.code===200){
                            //
                            this.searchHistoryData=[];
                        }
                    }
                });
            },
            //
            clearHistoryCancel() {
                console.log("");
            },
            //
            clearHistoryFn() {
                this.confirmShowStatus = true;
                //					vm.$dialog.confirm({
                //							message: '?',
                //							beforeClose: function(action, done) {
                //								console.log(action);
                //								done(false);
                //							}
                //						})
                //						.then(function() {
                //							console.log("");
                //							//
                //						})
                //						.catch(function() {
                //							console.log("");
                //							//
                //							vm.$dialog.close();
                //						})
            }, //
            wordSearchFn(obj) {
                // console.log(obj);
                // localStorage.setItem("globalSearchVal",JSON.stringify({
                //     val:obj.name,
                //     type:"keyword"
                // }));
                // //
                // this.$router.push({name:"searchResult"});
            },
            //
            backLastPage() {
               this.$router.go(-1);
            },
            //
            searchConfirm() {
                console.log(this.userSearchVal);
                if(this.userSearchVal===""){
                    this.tools.globalFn.publicToastFn(this,"");
                    return false;
                }
                this.publicSearchWordFn(this.userSearchVal);
                // //
                // localStorage.setItem("globalSearchVal",JSON.stringify({
                //     val:this.userSearchVal,
                //     type:"keyword"
                // }));
                // console.log(localStorage);
                // //
                // this.$router.push({name:"searchResult"});
            },
            //
            getFirstData(){
                //
                if(this.tools.globalFn.isLoginFn()===true&&this.$store.state.isDevMode===0){
                    this.tools.globalFn.publicAxiosFn(this,{
                        needLogin:true,
                        url:"/catalogsearch/index/searchrecord",
                        data:{
                            //
                            hotNumPerPage:10,
                            //
                            userNumPerPage:10
                        },
                        success:(res)=>{
                            console.log(res);
                            if(res.data.code===200){
                                //
                                this.hotSearchData=res.data.data.hotSearch;
                                //
                                this.searchHistoryData=res.data.data.userSearch;
                            }
                        }
                    });
                }
                //
                else if(this.tools.globalFn.isLoginFn()===false&&this.$store.state.isDevMode===0){
                    this.tools.globalFn.publicAxiosFn(this,{
                        // needLogin:true,
                        url:"/catalogsearch/index/searchrecord",
                        data:{
                            //
                            hotNumPerPage:10,
                            //
                            userNumPerPage:10
                        },
                        success:(res)=>{
                            console.log(res);
                            if(res.data.code===200){
                                //
                                this.hotSearchData=res.data.data.hotSearch;
                                //
                                this.searchHistoryData=res.data.data.userSearch;
                            }
                        }
                    });
                }
                else{
                    this.tools.globalFn.publicAxiosFn(this,{
                        needLogin:true,
                        url:"/catalogsearch/index/searchrecord",
                        data:{
                            //
                            hotNumPerPage:10,
                            //
                            userNumPerPage:10
                        },
                        success:(res)=>{
                            console.log(res);
                            if(res.data.code===200){
                                //
                                this.hotSearchData=res.data.data.hotSearch;
                                //
                                this.searchHistoryData=res.data.data.userSearch;
                            }
                        }
                    });
                }

            }
        },
        created() {
            this.getFirstData();
        },
        mounted() {

        },
        watch: {}
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

    .hotSearchKeywordBox {
        width: 100%;
        height: auto;
        overflow: hidden;
        box-sizing: border-box;
        padding: 10px;
        margin-bottom: 10px;
    }

    .hotSearchKeywordBox>p.header {
        width: 100%;
        color: #333;
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 6px;
    }

    .hotSearchKeywordBox>ul.list {
        width: 100%;
        height: auto;
        overflow: hidden;
        box-sizing: border-box;
    }

    .hotSearchKeywordBox>ul.list li {
        float: left;
        padding-right: 10px;
        padding-bottom: 10px;
    }

    .hotSearchKeywordBox>ul.list li p {
        padding: 4px 8px;
        color: #666;
        border-radius: 50px;
        background-color: #eaeaea;
        /*border: 1px solid #999;*/
        font-size: 14px;
    }

    .historySearchBox {
        width: 100%;
        height: auto;
        overflow: hidden;
        box-sizing: border-box;
        margin-bottom: 20px;
    }

    .historySearchBox>p.header {
        width: 100%;
        color: #333;
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 6px;
        padding: 0 10px;
    }

    .historySearchBox p.noContent,.hotSearchKeywordBox p.noContent {
        text-align: center;
        color: #999;
        font-size: 14px;
    }

    .clearHistoryBox {
        width: 90%;
        height: auto;
        overflow: hidden;
        box-sizing: border-box;
        margin: auto;
        margin-bottom: 20px;
    }


    .delButton{
        color: #FFFFFF;
        font-size: 15px;
        width: 65px;
        height: 44px;
        display: inline-block;
        text-align: center;
        line-height: 44px;
        background-color: #F44;
    }
</style>



