<template>
    <div class="publicAllContentBg" id="publishRating">
        <!---->
        <van-nav-bar class="globalNavBarHeaderClass" title=""
                     left-arrow
                     left-text=""
                     @click-left="backLastPage"
                     fixed
        >
            <!--<van-field left-icon="search" placeholder="" slot="title" v-model="userSearchVal" readonly></van-field>-->
            <!--<van-icon name="search" slot="right" style="color: #fafafa" @click="searchConfirm"></van-icon>-->
            <!--<span slot="right" style="color: #fafafa"></span>-->
        </van-nav-bar>
        <!--loading-->
        <van-loading type="spinner" color="black" v-if="firstDataStatusAnm===false"
                     class="globalLoadingAnmClass"></van-loading>
        <!---->
        <div class="singleGoodEvaBg">
            <div class="singleGoodEvaBox" v-for="(good,index) in goodContentData.list" :key="index">
                <!---->
                <div class="uploaderBg" v-if="firstDataStatusAnm===true">
                    <!---->
                    <div class="msRateBox">
                        <!---->
                        <div class="img">
                            <img :src="good.goodImg" alt="">
                        </div>
                        <!---->
                        <div class="rate">
                            <p class="text"></p>
                            <van-rate
                                    v-model="good.describeMatch"
                                    :count="5"
                                    color="#ffa133"
                                    void-color="#999"
                            >
                            </van-rate>
                        </div>
                        <!---->
                        <p class="text" v-text="returnLvTextFn(good.describeMatch)"></p>
                    </div>
                    <!---->
                    <van-field
                            v-model="good.goodEvaContent"
                            type="textarea"
                            placeholder=""
                            rows="3"
                            autosize
                    >
                    </van-field>
                    <!---->
                    <div class="content">
                        <!---->
                        <ul class="uploadImgListBox">
                            <li class="uploadedImg" v-for="(img,i) in good.imgList" :key="i">
                                <div class="uploadedImgBox">
                                    <img :src="img" alt="">
                                    <van-icon name="clear" @click="delThisPhoto(good,i,img)"></van-icon>
                                </div>
                            </li>
                            <li class="uploadLine" v-if="good.imgList.length<3">
                                <div class="uploadButton" @click="recordEditGoodIndex(index)">
                                    <div class="content">
                                        <van-uploader :after-read="onRead">
                                            <van-icon name="photograph" style="color: #999"></van-icon>
                                            <p></p>
                                            <p class="small">3</p>
                                        </van-uploader>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <!---->
                    <div class="anonymousBox">
                        <van-checkbox v-model="good.anonymous"></van-checkbox>
                        <p class="text" v-if="good.anonymous===false"></p>
                        <p class="text" v-if="good.anonymous===true"></p>
                    </div>
                </div>
            </div>
            <!---->
            <div class="shopRateBox" v-if="firstDataStatusAnm===true">
                <div class="header"><van-icon name="shop" style="color: #666;margin-right: 4px"></van-icon></div>
                <!---->
                <div class="content">
                    <p></p>
                    <van-rate
                            v-model="ratingContentData.shipService"
                            :count="5"
                            color="#ffa133"
                            void-color="#999"
                    >
                    </van-rate>
                    <p class="text" v-text="returnLvTextFn(ratingContentData.shipService)"></p>
                </div>
                <!---->
                <div class="content">
                    <p></p>
                    <van-rate
                            v-model="ratingContentData.attitude"
                            :count="5"
                            color="#ffa133"
                            void-color="#999"
                    >
                    </van-rate>
                    <p class="text" v-text="returnLvTextFn(ratingContentData.attitude)"></p>
                </div>
            </div>
        </div>
        <!---->
        <p class="submitButton" v-if="firstDataStatusAnm===true" @click="goToJFn"></p>
    </div>
</template>

<script>
    export default {
        name: 'publishRating',
        data() {
            return {
                //
                finalNeedJson:{
                    //
                    list:[],
                    //
                    shopStar:0,
                    //
                    shippingStar:0
                },
                goodContentData:{
                    list:[
                        // {
                        //     //id
                        //     goodId: good.product_id,
                        //     //
                        //     goodImg:good.imgUrl,
                        //     //
                        //     goodName:good.name,
                        //     //
                        //     goodEvaContent:"",
                        //     //
                        //     describeMatch:0,
                        //     //
                        //     imgList:[],
                        //     //
                        //     noName:false
                        // }
                    ],
                    //
                    current:0

                },
                //
                firstDataStatusAnm: true,
                //
                ratingContentData:{
                    //
                    shipService:0,
                    //
                    attitude:0
                },
                //
                orderInfo:{
                    orderNum:"",
                }
            }
        },
        methods: {
            //
            recordEditGoodIndex(index){
                console.log(index);
                this.goodContentData.current=index;
            },
            //
            goToJFn(){
                this.finalNeedJson.list=[];
                console.log(this.goodContentData.list);
                //
                if(this.ratingContentData.shipService===0){
                    this.tools.globalFn.publicToastFn(this,"");
                    return false;
                }
                if(this.ratingContentData.attitude===0){
                    this.tools.globalFn.publicToastFn(this,"");
                    return false;
                }
                //
                for(let good of this.goodContentData.list){
                    if(good.describeMatch===0){
                        this.tools.globalFn.publicToastFn(this,"");
                        return false;
                    }
                    if(good.goodEvaContent===""){
                        this.tools.globalFn.publicToastFn(this,"");
                        return false;
                    }
                    //
                    this.finalNeedJson.list.push({
                        productId:good.goodId,
                        custom_option_sku:good.skuName,
                        selectStar:good.describeMatch,
                        reviewContent:good.goodEvaContent,
                        review_image:good.imgList,
                        noName:good.anonymous===true?1:0
                    });
                }
                //
                this.finalNeedJson.shippingStar=this.ratingContentData.shipService;
                //
                this.finalNeedJson.shopStar=this.ratingContentData.attitude;
                console.log(this.finalNeedJson);
                //
                this.$dialog.confirm({
                    title: '',
                    message: ''
                })
                    .then(()=>{
                        //loading
                        this.tools.globalFn.publicFullScreenLoadingFn(this);
                        //
                        this.tools.globalFn.publicAxiosFn(this,{
                            url:"/catalog/reviewproduct/addpackreview",
                            needLogin:true,
                            data:{
                                //
                                packSn:this.orderInfo.orderNum,
                                //json
                                review:JSON.stringify(this.finalNeedJson)
                            },
                            success:(res)=>{
                                console.log(res);
                                if(res.data.code===200){
                                    this.tools.globalFn.publicToastFn(this,"");
                                    this.$router.go(-1);
                                }
                                else{
                                    this.tools.globalFn.publicToastFn(this,"");
                                }
                            }
                        });
                    });
            },
            //
            returnLvTextFn:function(val){
                console.log(val);
                if(val===0){
                    return "";
                }
                else if(val===1){
                    return "";
                }
                else if(val===2){
                    return "";
                }
                else if(val===3){
                    return "";
                }
                else if(val===4){
                    return "";
                }
                else if(val===5){
                    return "";
                }
            },
            //
            onRead(file) {
                console.log(this.tools.globalFn.makeUploadImgFormDataFn(5,file.file));
                console.log(file);
                //
                this.tools.globalFn.publicAxiosFn(this,{
                    url:"/backman/upload/images",
                    needLogin:true,
                    // data:this.tools.globalFn.makeUploadImgFormDataFn(5,file.file),
                    data:this.tools.globalFn.makeUploadImgFormDataFn(6,file.file),
                    success:(res)=>{
                        console.log(res);
                        if(res.data.code===200){
                            //(,)
                            if (res.data.code === 200) {
                                this.goodContentData.list[this.goodContentData.current].imgList.push(res.data.data.img_list[0]);
                            }
                        }
                        else{
                            this.tools.globalFn.publicToastFn(this,"");
                        }
                    }
                });
            },
            //
            delThisPhoto (good,index,item) {
                // console.log(index);
                // console.log(item);
                good.imgList.splice(index,1);
                // this.ratingContentData.ratingImg.pop(item);
            },
            //
            backLastPage () {
                this.$router.go(-1);
            },
            //
            getFirstData(){
                //loading
                this.tools.globalFn.publicFullScreenLoadingFn(this);
                //orderId
                this.tools.globalFn.publicAxiosFn(this,
                    {
                        needLogin:true,
                        url:"/customer/order/view",
                        method:"GET",
                        params:{
                            order_id:this.$route.params.id,
                            module_type:1,
                            is_pack:1
                        },
                        success:(res)=>{
                            console.log(res);
                            if(res.data.code===200){
                                //
                                this.orderInfo.orderNum=res.data.data.order.pack_sn;
                                //
                                for(let good of res.data.data.order.products){
                                    this.goodContentData.list.push({
                                        //id
                                        goodId: good.product_id,
                                        //
                                        goodImg:good.imgUrl,
                                        //
                                        goodName:good.name,
                                        //
                                        goodEvaContent:"",
                                        //fecshopsku
                                        skuName:good.custom_option_sku,
                                        //
                                        describeMatch:0,
                                        //
                                        imgList:[],
                                        //
                                        anonymous:false
                                    });
                                }
                                //loading
                                this.firstDataStatusAnm=true;
                                console.log(this.goodContentData.list);
                                //
                                this.$toast.clear();
                            }
                        }
                    }
                );
            }
        },
        created() {
            this.getFirstData();
        },
        mounted() {

        },
        watch: {}
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .singleGoodEvaBg{
        width: 100%;
        height:auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }
    .singleGoodEvaBox{
        width: 100%;
        height:auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        margin-top: 10px;
    }
    .singleGoodEvaBg>:first-child{
        margin-top: 0;
    }
    /**/
    .uploaderBg {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        background-color: #fff;
        padding: 10px 10px;
        margin-bottom: 10px;
    }

    .uploaderBg > div.header {
        color: #333;
        font-size: 14px;
        margin-bottom: 10px;
    }

    .uploaderBg > div.content {
        width: 100%;
        height: auto;
        /*overflow: hidden;*/
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        background-color: #fff;
        padding-bottom: 5px;
        margin-bottom: 5px;
        border-bottom: 1px solid #f3f3f3;
    }

    .uploadImgListBox {
        width: 100%;
        height: auto;
        /*overflow: hidden;*/
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        display: flex;
        justify-content: flex-start;
    }

    .uploadImgListBox > li {
        width: 25%;
        height: auto;
        /*overflow: hidden;*/
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        margin-left: 10px;
        /*float: left;*/
        /*padding: 0 10px;*/

    }

    .uploadImgListBox > li:first-child {
        margin-left: 0;
    }

    /**/
    .uploadButton {
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
        border: 1px dashed #999;
    }

    .uploadButton::before {
        padding-top: 100%;
        content: '\20';
        display: block;
    }

    .uploadButton > div.content {
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .uploadButton > div.content p {
        color: #999;
        text-align: center;
        font-size: 14px;
    }

    .uploadButton > div.content p.small {
        font-size: 12px;
    }

    /**/
    .uploadedImgBox {
        width: 100%;
        height: auto;
        padding-top: 100%;
        /*overflow: hidden;*/
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
    }

    .uploadedImgBox img {
        display: block;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        position: absolute;
    }

    .uploadedImgBox .van-icon {
        position: absolute;
        right: -7px;
        top: -7px;
        color: #000;
        opacity: 0.8;
        background-color: #fff;
        border-radius: 100%;
    }

    .submitButton {
        width: 90%;
        margin: auto;
        margin-bottom: 10px;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        border-radius: 2px;
        background: linear-gradient(to right, #30aaff, #2BD0FE);
        color: #fff;
        padding: 10px;
        text-align: center;
        font-size: 14px;
    }
    .uploaderBg .van-cell{
        padding: 0;
        margin-bottom: 10px;
    }
    [class*=van-hairline]::after{
        border: none;
    }
    .msRateBox{
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        display: flex;
        align-items:center;
        margin-bottom: 5px;
        padding-bottom: 5px;
        border-bottom: 1px solid #f3f3f3;
    }
    .msRateBox>div.img{
        width: 10%;
        padding-top: 10%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        position: relative;
    }
    .msRateBox>div.img img{
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
    .msRateBox>div.rate{
        color: #333;
        font-size: 14px;
        margin: 0 10px;
        display: flex;
    }
    .msRateBox>div.rate>p.text{
        font-size: 14px;
        color: #666;
        margin-right: 4px;
    }
    .msRateBox>p.text{
        color: #333;
        font-size: 14px;
    }
    /**/
    .anonymousBox{
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .anonymousBox>p.text{
        color: #999;
        font-size: 12px;
    }
    .anonymousBox .van-checkbox__label{
        color: #666;
        font-size: 12px;
    }
    .anonymousBox .van-checkbox__icon .van-icon{
        width: 14px;
        height: 14px;
    }
    .anonymousBox .van-checkbox__icon{
        line-height: 14px;
    }
    .anonymousBox .van-checkbox__icon--checked .van-icon{
        border-color: #ffa133;
        background-color: #ffa133;
    }
    /**/
    .shopRateBox{
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        background-color: #fff;
        padding: 10px;
        margin-bottom: 10px;
    }
    .shopRateBox>div.header{
        display: flex;
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        align-items: center;
        font-size: 14px;
        color: #666;
        padding-bottom: 5px;
        margin-bottom: 5px;
        border-bottom: 1px solid #f3f3f3;
    }
    .shopRateBox>div.content{
        display: flex;
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        align-items: center;
    }
    .shopRateBox>div.content>p{
        color: #666;
        font-size: 12px;
        margin: 0 0 0 4px;
    }
    .shopRateBox>div.content>p:first-child{
        margin: 0 4px 0 0;
    }
</style>



