    .addressBox > div.info > div.contact p.phone {
        color: #666;
        font-size: 14px;
    }

    .addressBox > div.info > p.address {
        color: #666;
        font-size: 14px;
        width: 100%;
        height: auto;
        overflow: hidden;
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        justify-content: space-between;
        word-break: break-all;
    }
</style>



