                     fixed
        >
        </van-nav-bar>
        <!---->
        <div class="contentColorBg">
            <p class="status" v-text="orderStatusText(orderContentData)"></p>
        </div>
        <van-loading type="spinner" color="black" v-if="orderLoadingDone===false"
                     class="globalLoadingAnmClass"></van-loading>
        <!---->
