        display: inline-block;
        color: #9b9b9b;
        font-size: 14px;
        border-radius: 2px;
        border: 1px solid #9b9b9b;
        padding: 4px 6px;
        float: right;
        margin-left: 4px;
    }

    .fixedBottomLine > span.pay {
        border-color: #30aaff;
        color: #30aaff;
    }

    .fixedBottomLine > span.del {
        float: left !important;
    }

    .fixedBottomLine > span.confirmGet {
